<template>
    <div class="Income">
      <div class="topPro">
        <h3><span>{{proFileInfo.total_income|moneyType}}</span>元</h3>
        <span class="info">累计收益</span>
      </div>
      <router-link to="/wdShouyi" tag="div" class="monthpro">
          <h3><span>{{proFileInfo.month_income|moneyType}}</span>元</h3>
          <span class="nowinfo">本月收益</span>
          <div class="monthList">
            <li class="mldetail">
              <div class="order">我的</div>
              <div class="money"><span>{{proFileInfo.mine|moneyType}}</span>元</div>
            </li>
            <li class="mldetail">
              <div class="order">一级</div>
              <div class="money"><span>{{proFileInfo.first|moneyType}}</span>元</div>
            </li>
            <li class="mldetail">
              <div class="order">二级</div>
              <div class="money"><span>{{proFileInfo.second|moneyType}}</span>元</div>
            </li>
        </div>
        <div class="iconfont icon-yousanjiao1"></div>
      </router-link>
      <ul class="distrb">
        <router-link :to="{'path':'/MyDistruIncome','query':{'grade':1}}"  tag="li">
        <div class="topList">
          <div class="tpleft">
            <span class="iconfont icon-fenxiao1"></span>
            <span class="fxlevel">一级分销</span>
          </div>
          <div class="lookMore">
            <span>{{proFileInfo.first_p_num}}人</span>
            <span class="iconfont icon-yousanjiao1"></span>
          </div>
        </div>
        <div class="fenxiaoList">
          <li>
            <span class="order">本月预约</span>
            <span class="money"><span>{{proFileInfo.first_ins_person_num}}</span>人</span>
          </li>
          <li>
            <span class="order">本月签约</span>
            <span class="money"><span>{{proFileInfo.first_qian_num}}</span>人</span>
          </li>
          <li>
            <span class="order">本月成交</span>
            <span class="money"><span>{{proFileInfo.first_money|moneyType}}</span>元</span>
          </li>
        </div>
        </router-link>
        <router-link :to="{'path':'/MyDistruIncome','query':{'grade':2}}"  tag="li">
        <div class="topList">
          <div class="tpleft">
            <span class="iconfont icon-fenxiao1"></span>
            <span class="fxlevel">二级分销</span>
          </div>
          <div class="lookMore">
            <span>{{proFileInfo.second_p_num}}人</span>
            <span class="iconfont icon-yousanjiao1"></span>
          </div>
        </div>
        <div class="fenxiaoList">
          <li>
            <span class="order">本月预约</span>
            <span class="money"><span>{{proFileInfo.second_ins_person_num}}</span>人</span>
          </li>
          <li>
            <span class="order">本月签约</span>
            <span class="money"><span>{{proFileInfo.second_qian_num}}</span>人</span>
          </li>
          <li>
            <span class="order">本月成交</span>
            <span class="money"><span>{{proFileInfo.second_money|moneyType}}</span>元</span>
          </li>
        </div>
        </router-link>
      </ul>
      <van-button type="default invate" @click="shareApp">邀请好友一起赚钱</van-button>
    </div>
</template>

<script>
import { apiGet } from '../common/js/api';
import { initWxConfig, isweixin } from "@/assets/utils"
import { Toast} from "vant";
    export default {
      name: "sx_profile",
      data(){
        return{
          proFileInfo:{
            totalMoney:5000,
            monthMoney:100,
            myMoney:5000
          },
          subList:[
            {
              typeName:"一级分销",
              num:300,
              ordered:200,
              signed:100,
              deal:1003,
              id:1
            },
            {
              typeName:"二级分销",
              num:300,
              ordered:200,
              signed:100,
              deal:1003,
              id:2
            },
            {
              typeName:"三级分销",
              num:300,
              ordered:200,
              signed:100,
              deal:1003,
              id:3
            }
          ]
        }
      },
      created(){
        let _this = this;
          apiGet("/index/sign-package",{
            url:encodeURIComponent("http://anfum.demo.sclonsee.com/IncomeProfile")
          }).then(res=>{
            let wxConfig = res.data.data.signpackage;
            wx.config({
                debug: false, // 开启调试模式,开发时可以开启  
                appId: wxConfig.appId,   // 必填，公众号的唯一标识   由接口返回
                timestamp: wxConfig.timestamp, // 必填，生成签名的时间戳 由接口返回
                nonceStr: wxConfig.nonceStr,    // 必填，生成签名的随机串 由接口返回
                signature: wxConfig.signature,   // 必填，签名 由接口返回
                jsApiList: ["onMenuShareAppMessage","checkJsApi"] // 此处填你所用到的方法 
              });
              wx.ready(function(resdata){
                console.log('ready',resdata)
                console.log('userId',_this.$cookie.get('id'))
                wx.onMenuShareAppMessage({ // 分享给朋友
                  title: "安富智享 用户邀请",       // 分享标题
                  desc: "注册加入安富智享，体验专业的保险服务",   // 分享描述
                  link: `http://anfum.demo.sclonsee.com/register?shareId=${_this.$cookie.get('id')}`,       // 分享链接 默认以当前链接
                  imgUrl:'http://pbu0dcbw1.bkt.clouddn.com/yao_anfu.jpg',// 分享图标
                  // 用户确认分享后执行的回调函数
                  success: function () {
                    // Toast("分享成功！")
                  },
                  // 用户取消分享后执行的回调函数
                  cancel: function () {
                    // Toast("分享失败!")
                  },
                  fail: function (res) {
                    alert(JSON.stringify(res));
                  }
                }); 
              })
          })
        this.getProfile();
      },
      methods:{
        //获取收益概览
        getProfile(){
          apiGet("/user/my-income",{
            accessToken:this.$cookie.get('accessToken')
          }).then(res=>{
            this.proFileInfo = res.data.data;
          })
        },
        //邀请好友
        shareApp(){
          console.log(wx)
          if(isweixin()){
            // wx.onMenuShareAppMessage({ // 分享给朋友
            //   title: "title",       // 分享标题
            //   desc: "desc",   // 分享描述
            //   link: `http://localhost:8080/register?shareId=${this.$cookie.get('userId')}`,       // 分享链接 默认以当前链接
            //   imgUrl:'/static/images/share.png',// 分享图标
            //   // 用户确认分享后执行的回调函数
            //   success: function () {
            //     Toast("分享成功！")
            //   },
            //   // 用户取消分享后执行的回调函数
            //   cancel: function () {
            //     Toast("分享失败!")
            //     console.log('分享到朋友取消');
            //   }
            // });
          }else{
            Toast("请在微信浏览器中打开！")
          }
          
        }
      }
    }
</script>

<style scoped lang="scss">
  .Income{
    height: 100%;
    background: #E6E9ED;
    overflow: auto;
    .topPro{
      height: 2.18rem;
      padding-top: 0.53rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(127,173,249,1));
      h3{
        font-size: 0.31rem;
        color: white;
        text-align: center;
      }
      span.info{
        text-align: center;
        display: block;
        color:rgba(255,255,255,.3);
      }
    }
    .monthpro{
      position: relative;
      width: 3.5rem;
      margin: 0 auto;
      background: white;
      padding: 0.4rem 0.13rem 0;
      border-radius: 0.04rem;
      top:-0.67rem;
      min-height: 1.7rem;
      .iconfont{
        position: absolute;
        color: #d7d7d7;
        right: 0;
        top:43%
      }
      h3{
        color: #3C7FEE;
        font-size: 0.4rem;
      }
      .nowinfo{
        font-size: 0.15rem;
        color: #9a9a9a;
        padding-left: 0.12rem;
      }
      .monthList{
        height: 0.68rem;
        margin-top: 0.3rem;
        border-top: 1px solid #f5f5f5;
        display: flex;
        justify-content: space-between;
        li{
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          .order{
            font-size: 0.15rem;
            color: #9a9a9a;
          }
          .money{
            font-size: 0.17rem;
            color: #333;
          }
        }
      }
    }
    .distrb{
      width: 3.5rem;
      margin: 0 auto;
      position: relative;
      top: -0.3rem;
      li{
        padding: 0.17rem 0.13rem 0.12rem;
        background:white ;
        border-radius: 0.04rem;
        margin-bottom: 0.17rem;
        .topList{
          height: 0.47rem;
          display: flex;
          justify-content: space-between;
          width: 100%;
          .tpleft{
            display: flex;
            align-items: center;
              .iconfont{font-size: 0.26rem;color: #F8D442;}
              .fxlevel{font-size: 0.15rem;color: #333;font-weight: 400}
          }
          .lookMore{
           display: flex;
            align-items: center;
            color: #9a9a9a;
            span.money{
              display: flex;
              align-items: flex-start;
            }
            .iconfont{
              margin-left: 0.08rem;
            }
          }
        }
        .fenxiaoList{
          height: 0.68rem;
          border-top: 1px solid #f5f5f5;
          display: flex;
          justify-content: space-between;
          li{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: 0.3rem;
            .order{
              color: #9a9a9a;
              font-size: 0.15rem;
            }
            .money{
              font-size: 0.17rem;
              color: #333;
            }
          }
        }
      }
    }
  }
  .invate{
    width: 100%;
    height:0.47rem;
    font-size: 0.19rem;
    color: white;
    text-align: center;
    background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
  }
</style>



